class KeyListener {
    constructor(code) {
        this.isDown = false;
        this.isUp = true;
        this.code = code;
    }
    static create(keyCode, down, up) {
        const keyListener = new KeyListener(keyCode);
        // eslint-disable-next-line @typescript-eslint/no-unsafe-argument
        window.addEventListener('keydown', keyListener.downHandler.bind(keyListener), false);
        // eslint-disable-next-line @typescript-eslint/no-unsafe-argument
        window.addEventListener('keyup', keyListener.upHandler.bind(keyListener), false);
        if (down) {
            keyListener.press = down;
        }
        if (up) {
            keyListener.release = up;
        }
        return keyListener;
    }
    downHandler(event) {
        if (event.keyCode === this.code) {
            if (this.isUp && this.press)
                this.press();
            this.isDown = true;
            this.isUp = false;
            event.preventDefault();
        }
    }
    upHandler(event) {
        if (event.keyCode === this.code) {
            if (this.isDown && this.release)
                this.release();
            this.isDown = false;
            this.isUp = true;
            event.preventDefault();
        }
    }
}

